<template>
  <div class="row">
    <div class="col-9">
      <h1 class="h3">
        Radioify for
        <img class="logo" src="@/assets/spotify/Spotify_Logo_RGB_Green.png" />
      </h1>
    </div>
    <div class="col mt-4 text-end">
      <button
        v-show="getToken"
        class="btn btn-link pt-0 mt-0"
        @click="clearToken()"
      >
        Logout
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "TheHeader",
  props: {},
  computed: {
    ...mapGetters(["getToken"]),
  },
  methods: {
    ...mapActions(["setToken"]),
    clearToken() {
      this.setToken("");
      this.$router.push({ name: "Main" });
    },
  },
};
</script>

